<template>
  <section class="countdown">
    <div class="alignment">
      <a :href="url">
        <img width="35" height="35" alt="" :src="imgSrc" />
      </a>
    </div>
    <div class="alignment">
      <a :href="url">
        <span v-if="time.days >= 1">
          <span v-for="char in time.days" :key="char">
            <span class="countdown-box">{{ char }}</span>&nbsp;
          </span>DAY<span v-if="time.days > 1">S</span><br />To {{copTitle}}
        </span>
        <span v-else>{{copTitle}}</span>
      </a>
    </div>
  </section>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from 'vue';

const { copTitle, endDate, url, imageSource } = defineProps({
  copTitle: {type: String, required: true},
  endDate:  {type: Date,   required: true},
  url:      {type: String, required: false},
  imageSource:{type: String, required: false}
  
});

const currentDate = ref(new Date());
const timer = ref(null);
let imgSrc = '';


const getNumberDays = () => {
  const countDownDate = endDate.getTime();
  const now = currentDate.value.getTime();
  const distance = countDownDate - now;
  const days = Math.ceil(distance / (1000 * 60 * 60 * 24)).toString().padStart(2, "0");
  const hours = Math.ceil((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)).toString().padStart(2, "0");
  const minutes = Math.ceil((distance % (1000 * 60 * 60)) / (1000 * 60)).toString().padStart(2, "0");
  const seconds = Math.ceil((distance % (1000 * 60)) / 1000).toString().padStart(2, "0");

  return { days, hours, minutes, seconds };
};

const time = getNumberDays();

onMounted(() => {
  timer.value = setInterval(() => {
    currentDate.value = new Date();
  }, 1000 * 60);
});

onBeforeUnmount(() => {
  clearInterval(timer.value);
});
    //An image should be passed in props
    if(imageSource){
      imgSrc = imageSource;
    }
    else {
    imgSrc = 'data:image/png;base64,';
    }
</script>

<style scoped>
a {
  color: inherit;
  text-decoration: none;
}

.countdown {
  background-color: #13a89e;
  width: 120px;
  text-align: center;
  display: flex;
  font-family: Arial Black, Arial, Helvetica, sans-serif;
  font-style: bold;
  font-size: 12px;
  color: #ffffff;
  padding-top: 1px;
  padding-left: 0px;
  padding-right: 5px;
  padding-bottom: 0px;
}

.alignment {
  padding: 1px;
  display: block;
  margin: auto;
}

.countdown-box {
  border: solid #ffffff 1px;
  padding: 0.5px;
}
</style>
